<template>
    <div>
        <wde></wde>
        <!--1-->
        <mt-field label="" :placeholder="unameholder" v-model="uname" :attr="{maxlength:12}"></mt-field>
        <!--2-->
        <mt-field label="" :placeholder="upwdholder" v-model="upwd" :attr="{maxlength:16}" type="password"></mt-field>
        <!--3-->
        <logins v-show="play"></logins>
        <!--4-->
        <mt-button size="large" @click="login"></mt-button>
    </div>
</template>
<script>
//silde-vertfy
import Logins from "../../../login/src/lib/Logins"
import Wde from './Wde'
export default {
    data(){
        return{
            unameholder:"",
            uname:"",
            upwdholder:"",
            upwd:"",
            play:false,
        }
    },
    components:{
        "logins":Logins,
        "wde":Wde
    },
    methods:{
        login(){
            //
            //1
            var u=this.uname;
            //2
            var p=this.upwd;
            //3
            var reg=/^[\u4e00-\u9fa5a-z0-9]{2,12}/g;
            var regs=/[a-z0-9]{6,16}/
            //4  
            if(!reg.test(u)){
                this.$toast("");
                return;//
            }else if(!regs.test(p)){//5  
                this.$toast("");
                return; //
            }else{//6ajax  axios
                var url="login";
                var obj={uname:u,upwd:p};
                this.axios.get(url,{params:obj}).then(res=>{
                    //7
                    console.log(res);
                    // 
                    if(res.data.code==-1){
                        this.$toast("")
                    }else{
                        //
                        //vue 
                        this.play=true;
                    }
                })
            }    
        }
    },
    created(){
        console.log(window.history.length);
    }
}
</script>
