<template>
    <div>
        <zhuce></zhuce>
        <!--1-->
        <mt-field label="" :placeholder="unameholder" v-model="uname" :attr="{maxlength:12}"></mt-field>
        <!--2-->
        <mt-field label="" :placeholder="upwdholder" v-model="upwd" :attr="{maxlength:16}" type="password"></mt-field>
        <!--3-->
        <mt-field label="" v-model="yz" :placeholder="holder" @focus.native.capture="checkCard"></mt-field>
        <!--4-->

        <!--5-->
        <mt-button size="large" @click="reg"></mt-button>
        <div @click="refreshCode" v-show="yc">
            <s-identify :identifyCode="identifyCode" v-model="yz"></s-identify>
            <a href="#" class="yz">&nbsp;</a>
        </div>
    </div>
</template>

<script>
import Identify from "./Identify"
import Zhuce from "./Zhuce"
export default {
  name: "codetest",
  data() {
    return {
      identifyCodes: "1234567890qwertyuiopasdfghjklzxcvbnmQWERTYUIOPASDFGHJKLZXCVBNM",
      identifyCode: "",
      unameholder:"",
      uname:"",
      upwdholder:"",
      holder:"",
      upwd:"",
      yz:"",
      yc:false,
      identifyCode:"",
    };
  },
  mounted() {
    this.identifyCode = "";
    this.makeCode(this.identifyCodes, 4);
  },
  components:{
        "s-identify":Identify,
        "zhuce":Zhuce
  },
    methods: {
        checkCard() {
            this.yc=true;
        },
        Card(){
            this.yc=false;
        },
        randomNum(min, max) {
            return Math.floor(Math.random() * (max - min) + min);
        },
        refreshCode() {
            this.identifyCode = "";
            this.makeCode(this.identifyCodes, 4);
        },
        makeCode(o, l) {
            for (let i = 0; i < l; i++) {
                this.identifyCode += this.identifyCodes[
                    this.randomNum(0, this.identifyCodes.length)
                ];
            }
            this.identifyCode=this.identifyCode.toLowerCase();
            console.log(this.identifyCode);
        },
        reg(){
            //
            //1
            var u=this.uname;
            //2
            var p=this.upwd;
            //3
            var reg=/^[\u4e00-\u9fa5a-z0-9]{2,12}/g;
            var regs=/[a-z0-9]{6,16}/
            //4  
            if(!reg.test(u)){
                this.$toast("");
                return;//
            }else if(!regs.test(p)){//5  
                this.$toast("");
                return; //
            }else{//6ajax  axios
                var url="reg";
                var obj={uname:u,upwd:p};
                this.axios.get(url,{params:obj}).then(res=>{
                    //7
                    console.log(res);
                    // 
                    if(res.data.code==-1){
                        this.$toast("")
                    }else{
                        //  
                        //vue 
                        if(this.identifyCode==this.yz){
                            this.$router.push("/Login")
                        }else{
                            //
                            this.$toast("");
                        }
                    }
                })
            }    
        },
    }
};
// Vue.directive('mintblur', { // directiveblur@blur.native.capture="cardNoBlur"
//     inserted: function(el, pra, a) {
//         let oInput = el.querySelector('input');
//             console.log('oInput', oInput);
//             oInput.onfocus = function() {
//             // focus
//         };
//         oInput.onblur = function() {
//             console.log('blu1r');
//             this.$emit(pra.expression);
//         };
//     }
// })
</script>

<style>
    .yz{
        position: relative;
        left: 50%;
        top: -30px;
    }
</style>
